import React from 'react';
import { useT } from '../i18n/LanguageProvider.jsx';
import { useReveal } from '../hooks/useReveal.js';

export default function Join() {
  const t = useT();
  function copyIP(e) {
    e.preventDefault();
    navigator.clipboard.writeText('mc.gangui.eu').catch(() => {});
  }
  useReveal();
  return (
    <section id="join" className="join section-auto-fade">
      <div className="container">
        <div className="join-content">
          <div className="section-header">
            <h2 data-lang="join-title" tabIndex="-1">{t('join-title')}</h2>
            <p data-lang="join-subtitle">{t('join-subtitle')}</p>
          </div>
          <div className="join-steps">
            <div
              className="join-step elevated gradient-border hover-scale-sm"
              onClick={copyIP}
              style={{ cursor: 'pointer' }}
            >
              <div className="step-number">
                <i className="fas fa-copy" />
              </div>
              <div className="step-content">
                <h3 data-lang="step1-title">{t('step1-title')}</h3>
                <p
                  data-lang-html="step1-desc"
                  dangerouslySetInnerHTML={{ __html: t('step1-desc', { html: true }) }}
                />
              </div>
            </div>
            <div className="join-step elevated gradient-border hover-scale-sm">
              <div className="step-number">
                <i className="fas fa-plus-square" />
              </div>
              <div className="step-content">
                <h3 data-lang="step2-title">{t('step2-title')}</h3>
                <p data-lang="step2-desc">{t('step2-desc')}</p>
              </div>
            </div>
            <div className="join-step elevated gradient-border hover-scale-sm">
              <div className="step-number">
                <i className="fas fa-play" />
              </div>
              <div className="step-content">
                <h3 data-lang="step3-title">{t('step3-title')}</h3>
                <p data-lang="step3-desc">{t('step3-desc')}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="join-bg" />
    </section>
  );
}
