import React, { useEffect, useRef, useState } from 'react';
import { useT } from '../i18n/LanguageProvider.jsx';
import { useReveal } from '../hooks/useReveal.js';

export default function Features() {
  const t = useT();
  const carouselRef = useRef(null);
  const dotsRef = useRef(null);
  const [current, setCurrent] = useState(0);
  const [paused, setPaused] = useState(false);

  const showSlide = (i, smooth = true) => {
    const carousel = carouselRef.current;
    if (!carousel) return;
    const cards = Array.from(carousel.querySelectorAll('.feature-card'));
    if (cards.length === 0) return;
    const idx = ((i % cards.length) + cards.length) % cards.length;
    setCurrent(idx);
    const target = cards[idx];
    // Center the target card within the carousel viewport
    const left =
      target.offsetLeft - carousel.offsetLeft - (carousel.clientWidth - target.clientWidth) / 2;
    carousel.scrollTo({ left, behavior: smooth ? 'smooth' : 'auto' });
  };

  useEffect(() => {
    const carousel = carouselRef.current;
    if (!carousel) return;
    const cards = Array.from(carousel.querySelectorAll('.feature-card'));
    function ensureDots() {
      const dotsC = dotsRef.current;
      if (!dotsC) return;
      if (dotsC.children.length !== cards.length) {
        dotsC.innerHTML = '';
        cards.forEach((_, i) => {
          const span = document.createElement('span');
          span.className = 'carousel-dot';
          span.addEventListener('click', () => showSlide(i));
          dotsC.appendChild(span);
        });
      }
    }
    function syncActive() {
      cards.forEach((c, i) => c.classList.toggle('active', i === current));
      const dotsC = dotsRef.current;
      if (!dotsC) return;
      Array.from(dotsC.children).forEach((d, i) => d.classList.toggle('active', i === current));
    }
    ensureDots();
    syncActive();
    let scrollRaf = null;
    const onScroll = () => {
      if (scrollRaf) cancelAnimationFrame(scrollRaf);
      scrollRaf = requestAnimationFrame(() => {
        const center = carousel.scrollLeft + carousel.offsetWidth / 2;
        let best = 0;
        let bestDiff = Infinity;
        cards.forEach((card, i) => {
          const cardCenter = card.offsetLeft - carousel.offsetLeft + card.offsetWidth / 2;
          const diff = Math.abs(center - cardCenter);
          if (diff < bestDiff) {
            bestDiff = diff;
            best = i;
          }
        });
        if (best !== current) setCurrent(best);
      });
    };
    const handleKeyDown = (e) => {
      if (e.key === 'ArrowLeft') showSlide(current - 1);
      if (e.key === 'ArrowRight') showSlide(current + 1);
    };
    // Pause auto-advance on hover/focus within
    const onEnter = () => setPaused(true);
    const onLeave = () => setPaused(false);
    const onFocusIn = () => setPaused(true);
    const onFocusOut = () => setPaused(false);
    // Block horizontal wheel/trackpad scroll on the carousel (keep page vertical scroll)
    const onWheel = (e) => {
      const absX = Math.abs(e.deltaX);
      const absY = Math.abs(e.deltaY);
      if (absX > absY) {
        e.preventDefault();
      }
    };
    // No mouse wheel handling: keep page scroll natural; use dots/click/keys to navigate
    carousel.addEventListener('scroll', onScroll);
    carousel.addEventListener('keydown', handleKeyDown);
    carousel.addEventListener('mouseenter', onEnter);
    carousel.addEventListener('mouseleave', onLeave);
    carousel.addEventListener('focusin', onFocusIn);
    carousel.addEventListener('focusout', onFocusOut);
    carousel.addEventListener('wheel', onWheel, { passive: false });
    const resize = () => showSlide(current, false);
    window.addEventListener('resize', resize);
    return () => {
      carousel.removeEventListener('scroll', onScroll);
      carousel.removeEventListener('keydown', handleKeyDown);
      carousel.removeEventListener('mouseenter', onEnter);
      carousel.removeEventListener('mouseleave', onLeave);
      carousel.removeEventListener('focusin', onFocusIn);
      carousel.removeEventListener('focusout', onFocusOut);
      carousel.removeEventListener('wheel', onWheel);
      // no wheel handler attached
      window.removeEventListener('resize', resize);
    };
  }, [current]);

  useEffect(() => {
    const carousel = carouselRef.current;
    if (!carousel) return;
    const cards = Array.from(carousel.querySelectorAll('.feature-card'));
    cards.forEach((c, i) => c.classList.toggle('active', i === current));
    const dotsC = dotsRef.current;
    if (dotsC)
      Array.from(dotsC.children).forEach((d, i) => d.classList.toggle('active', i === current));
  }, [current]);

  useReveal();
  // Auto-advance every 5s; pause when hovered/focused or when page hidden
  useEffect(() => {
    const handleVis = () => setPaused(document.hidden);
    document.addEventListener('visibilitychange', handleVis);
    const id = setInterval(() => {
      if (!paused) {
        showSlide(current + 1);
      }
    }, 3000);
    return () => {
      clearInterval(id);
      document.removeEventListener('visibilitychange', handleVis);
    };
  }, [current, paused]);
  return (
    <section id="features" className="features section-auto-fade">
      <div className="container">
        <div className="section-header">
          <h2 data-lang="features-title" tabIndex="-1">{t('features-title')}</h2>
          <p data-lang="features-subtitle">{t('features-subtitle')}</p>
        </div>
        <div className="features-carousel-wrapper">
          <div
            className="features-grid carousel"
            ref={carouselRef}
            tabIndex={0}
            style={{ outline: 'none' }}
          >
            <div
              className="feature-card elevated gradient-border hover-scale-sm"
              onClick={() => showSlide(0)}
            >
              <div className="feature-icon">
                <i className="fas fa-bolt" />
              </div>
              <h3 data-lang="feature1-title">{t('feature1-title')}</h3>
              <p data-lang="feature1-desc">{t('feature1-desc')}</p>
            </div>
            <div
              className="feature-card elevated gradient-border hover-scale-sm"
              onClick={() => showSlide(1)}
            >
              <div className="feature-icon">
                <i className="fas fa-user-secret" />
              </div>
              <h3 data-lang="feature2-title">{t('feature2-title')}</h3>
              <p data-lang="feature2-desc">{t('feature2-desc')}</p>
            </div>
            <div
              className="feature-card elevated gradient-border hover-scale-sm"
              onClick={() => showSlide(2)}
            >
              <div className="feature-icon">
                <i className="fas fa-robot" />
              </div>
              <h3 data-lang="feature3-title">{t('feature3-title')}</h3>
              <p data-lang="feature3-desc">{t('feature3-desc')}</p>
            </div>
            <div
              className="feature-card elevated gradient-border hover-scale-sm"
              onClick={() => showSlide(3)}
            >
              <div className="feature-icon">
                <i className="fas fa-layer-group" />
              </div>
              <h3 data-lang="feature4-title">{t('feature4-title')}</h3>
              <p data-lang="feature4-desc">{t('feature4-desc')}</p>
            </div>
          </div>
          <div className="carousel-dots" ref={dotsRef} />
        </div>
      </div>
    </section>
  );
}
