import React, { useState } from 'react';
import { useT } from '../i18n/LanguageProvider.jsx';
import { useReveal } from '../hooks/useReveal.js';

const items = [
  { qKey: 'faq1-question', aKey: 'faq1-answer' },
  { qKey: 'faq2-question', aKey: 'faq2-answer' },
  { qKey: 'faq3-question', aKey: 'faq3-answer' },
  { qKey: 'faq4-question', aKey: 'faq4-answer' },
  { qKey: 'faq5-question', aKey: 'faq5-answer' },
];

export default function FAQ() {
  const t = useT();
  const [open, setOpen] = useState(null);
  useReveal();
  return (
    <section id="faq" className="faq section-auto-fade">
      <div className="container">
        <div className="section-header">
          <h2 data-lang="faq-title" tabIndex="-1">{t('faq-title')}</h2>
          <p data-lang="faq-subtitle">{t('faq-subtitle')}</p>
        </div>
        <div className="faq-content">
          {items.map((it, i) => {
            const active = open === i;
            return (
              <div
                key={it.qKey}
                className={'faq-item gradient-border hover-scale-sm' + (active ? ' active' : '')}
              >
                <div className="faq-question" onClick={() => setOpen(active ? null : i)}>
                  <h3 data-lang={it.qKey}>{t(it.qKey)}</h3>
                  <div className="faq-toggle">
                    <i className="fas fa-chevron-down" />
                  </div>
                </div>
                <div className="faq-answer" style={active ? { maxHeight: '3000px' } : undefined}>
                  <p data-lang={it.aKey}>{t(it.aKey)}</p>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
}
