import React from 'react';
import { Helmet } from 'react-helmet';
import Header from '../components/Header.jsx';
import Footer from '../components/Footer.jsx';
import Hero from '../components/Hero.jsx';
import Features from '../components/Features.jsx';
import FAQ from '../components/FAQ.jsx';
import Join from '../components/Join.jsx';
import Stats from '../components/Stats.jsx';
import { useEffect, useState } from 'react';

function BackToTop() {
  return (
    <button
      className="back-to-top"
      aria-label="Back to top"
      onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}
    >
      <i className="fas fa-chevron-up" />
    </button>
  );
}

export default function HomePage() {
  const [hash, setHash] = useState(typeof window !== 'undefined' ? window.location.hash : '#hero');
  useEffect(() => {
    function onHash() {
      setHash(window.location.hash || '#hero');
    }
    window.addEventListener('hashchange', onHash);
    return () => window.removeEventListener('hashchange', onHash);
  }, []);
  // Ensure smooth scroll to the target after conditional sections mount
  useEffect(() => {
    if (typeof window === 'undefined') return;
    const current = hash || '#hero';
    // When going to #stats, scroll to top (stats view replaces sections)
    if (current === '#stats') {
      window.scrollTo({ top: 0, behavior: 'smooth' });
      // Focus on the stats section heading
      setTimeout(() => {
        const statsHeading = document.querySelector('#stats h1, #stats h2, #stats [role="heading"]');
        if (statsHeading) {
          statsHeading.focus({ preventScroll: true });
        }
      }, 300); // After scroll animation
      return;
    }
    // For other anchors, the first click may change the hash while the target wasn't in the DOM yet
    // Since this effect runs after render, the sections are now mounted: scroll into view explicitly
    const el = document.querySelector(current);
    if (el) {
      // Use block:start and rely on css scroll-padding-top for header offset
      el.scrollIntoView({ behavior: 'smooth', block: 'start' });
      // Focus on the section's heading for accessibility
      setTimeout(() => {
        const heading = el.querySelector('h1, h2, h3, [role="heading"]');
        if (heading) {
          heading.focus({ preventScroll: true });
        }
      }, 300); // After scroll animation
    }
  }, [hash]);
  // Clear ?member param when leaving stats section for clean URLs
  useEffect(() => {
    if (hash !== '#stats') {
      try {
        const u = new URL(window.location.href);
        if (u.searchParams.has('member')) {
          u.searchParams.delete('member');
          window.history.replaceState({}, '', u.toString());
        }
      } catch (_) {
        /* ignore */
      }
    }
  }, [hash]);
  const showStats = hash === '#stats';

  // Dynamic meta tags for SEO
  const getMetaForSection = (section) => {
    const baseMeta = {
      title: 'Gangui - Minecraft Server',
      description: 'Join the ultimate Minecraft experience on Gangui server. Community-driven gameplay with unique features and adventures.',
      url: `${window.location.origin}${window.location.pathname}${section}`,
    };

    switch (section) {
      case '#hero':
        return {
          ...baseMeta,
          title: 'Gangui - Home | Minecraft Server',
          description: 'Welcome to Gangui, the premier Minecraft server for community gaming, unique adventures, and competitive gameplay.',
        };
      case '#features':
        return {
          ...baseMeta,
          title: 'Features | Gangui Minecraft Server',
          description: 'Discover the amazing features of Gangui Minecraft server: custom game modes, competitive gameplay, and community events.',
        };
      case '#faq':
        return {
          ...baseMeta,
          title: 'FAQ | Gangui Minecraft Server',
          description: 'Frequently asked questions about Gangui Minecraft server. Learn how to join, gameplay rules, and server information.',
        };
      case '#join':
        return {
          ...baseMeta,
          title: 'Join Now | Gangui Minecraft Server',
          description: 'Ready to join Gangui? Get the server IP, learn how to connect, and start your Minecraft adventure today.',
        };
      case '#stats':
        return {
          ...baseMeta,
          title: 'Player Stats | Gangui Minecraft Server',
          description: 'View detailed player statistics, rankings, and achievements on the Gangui Minecraft server.',
        };
      default:
        return baseMeta;
    }
  };

  const currentMeta = getMetaForSection(hash);

  return (
    <>
      <Helmet>
        <title>{currentMeta.title}</title>
        <meta name="description" content={currentMeta.description} />
        <meta property="og:title" content={currentMeta.title} />
        <meta property="og:description" content={currentMeta.description} />
        <meta property="og:url" content={currentMeta.url} />
        <meta property="og:type" content="website" />
        <meta name="twitter:card" content="summary" />
        <meta name="twitter:title" content={currentMeta.title} />
        <meta name="twitter:description" content={currentMeta.description} />
        <link rel="canonical" href={currentMeta.url} />
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "Organization",
            "name": "Gangui",
            "description": "Minecraft server offering community-driven gameplay with unique features and adventures",
            "url": window.location.origin,
            "logo": `${window.location.origin}/img/logo_dark.svg`,
            "sameAs": [
              "https://discord.gg/gangui", // Assuming Discord link
              "https://twitter.com/gangui" // Assuming Twitter link
            ],
            "game": {
              "@type": "VideoGame",
              "name": "Minecraft",
              "genre": "Sandbox",
              "playMode": "MultiPlayer"
            },
            "areaServed": "Worldwide",
            "serviceType": "Gaming Server"
          })}
        </script>
      </Helmet>
      <Header />
      <main style={{ paddingTop: '120px' }}>
        {!showStats && <Hero />}
        {!showStats && <Features />}
        {!showStats && <FAQ />}
        {!showStats && <Join />}
        {showStats && <Stats />}
      </main>
      <Footer />
      <BackToTop />
    </>
  );
}
