import { useCallback, useEffect, useRef, useState } from 'react';

// Public error codes used by the hook
export const ERROR_CODES = {
  EMPTY: 'empty',
  NOT_FOUND: 'notfound',
  ERROR: 'error',
  RATE_LIMIT: 'ratelimited',
};

// Simple translation helper (fallback only)
const t = (key, fallback) => fallback || key;

// No local mock DB: rely on backend if configured, otherwise Mojang only

// Optional API base (configure with VITE_API_BASE_URL), defaults to same-origin
const API_BASE =
  (typeof import.meta !== 'undefined' && import.meta.env && import.meta.env.VITE_API_BASE_URL) ||
  '';

function formatDate(timestamp) {
  if (!timestamp) return t('stats-na', 'N/A');
  const d = new Date(timestamp);
  return d.toLocaleDateString();
}
function formatPlayTime(ms) {
  if (ms == null) return t('stats-na', 'N/A');
  let s = Math.floor(ms / 1000);
  let m = Math.floor(s / 60);
  let h = Math.floor(m / 60);
  s %= 60;
  m %= 60;
  return `${h > 0 ? h + 'h ' : ''}${m > 0 ? m + 'm ' : ''}${s}s`.trim();
}
function formatUUID(uuid) {
  if (!uuid || uuid.length !== 32) return uuid || 'N/A';
  return `${uuid.substring(0, 8)}-${uuid.substring(8, 12)}-${uuid.substring(12, 16)}-${uuid.substring(16, 20)}-${uuid.substring(20)}`;
}

// --- Profile cache (memory + localStorage) ---
const CACHE_KEY = 'mcProfileCache';
const profileCache = { usernameToUuid: {}, uuidToName: {} };
// Load cache from localStorage
try {
  if (typeof window !== 'undefined' && typeof localStorage !== 'undefined') {
    const raw = localStorage.getItem(CACHE_KEY);
    if (raw) Object.assign(profileCache, JSON.parse(raw));
  }
} catch (e) {
  console.error('Failed to load profile cache from localStorage:', e);
}
function saveProfileCache() {
  try {
    if (typeof window !== 'undefined' && typeof localStorage !== 'undefined') {
      localStorage.setItem(CACHE_KEY, JSON.stringify(profileCache));
    }
  } catch (e) {
    console.error('Failed to save profile cache to localStorage:', e);
  }
}

// --- Username → UUID (playerdb.co, fallback Mojang, cache) ---
async function getProfileFromUsername(username) {
  const uname = username.toLowerCase();
  if (profileCache.usernameToUuid[uname]) return profileCache.usernameToUuid[uname];
  // 1. playerdb.co
  try {
    const resp = await fetch(
      `https://playerdb.co/api/player/minecraft/${encodeURIComponent(uname)}`
    );
    if (resp.ok) {
      const data = await resp.json();
      const p = data?.data?.player;
      if (data?.success && p?.id && p?.username) {
        const result = { uuid: p.id.replace(/-/g, ''), name: p.username };
        profileCache.usernameToUuid[uname] = result;
        profileCache.uuidToName[result.uuid] = result.name;
        saveProfileCache();
        return result;
      }
    }
  } catch {}
  // 2. Fallback Mojang official API
  try {
    const resp = await fetch(
      `https://api.mojang.com/users/profiles/minecraft/${encodeURIComponent(uname)}`
    );
    if (!resp.ok) return null;
    const data = await resp.json();
    if (data?.id && data?.name) {
      const result = { uuid: data.id.replace(/-/g, ''), name: data.name };
      profileCache.usernameToUuid[uname] = result;
      profileCache.uuidToName[result.uuid] = result.name;
      saveProfileCache();
      return result;
    }
  } catch {}
  return null;
}

// --- UUID → Username (Mojang, cache) ---
async function getProfileFromUUID(uuid) {
  const clean = uuid.replace(/-/g, '');
  if (profileCache.uuidToName[clean]) return { name: profileCache.uuidToName[clean] };
  try {
    // Mojang sessionserver returns full profile including name
    const resp = await fetch(`https://sessionserver.mojang.com/session/minecraft/profile/${clean}`);
    if (!resp.ok) return null;
    const data = await resp.json();
    if (data?.name) {
      profileCache.uuidToName[clean] = data.name;
      saveProfileCache();
      return { name: data.name };
    }
  } catch {}
  return null;
}

// --- Skin URL helper ---
function getSkinUrl(uuid) {
  if (!uuid) return '/img/player-skin.png';
  const clean = uuid.replace(/-/g, '');
  return `https://crafatar.com/skins/${clean}?default=MHF_Steve&size=256`;
}

function getCandidateSkinUrls(uuid) {
  const clean = (uuid || '').replace(/-/g, '');
  if (!clean || !/^[0-9a-fA-F]{32}$/.test(clean)) {
    return ['/img/player-skin.png'];
  }
  return [
    `https://mc-heads.net/skin/${clean}`,
    `https://minotar.net/skin/${clean}`,
    `https://crafatar.com/skins/${clean}?size=256&default=MHF_Steve`,
    '/img/player-skin.png',
  ];
}

async function resolveFirstWorkingImage(urls) {
  for (const url of urls) {
    try {
      const ok = await new Promise((resolve) => {
        const img = new Image();
        let settled = false;
        const settle = (res) => {
          if (!settled) {
            settled = true;
            resolve(res);
          }
        };
        img.onload = () => settle(true);
        img.onerror = () => settle(false);
        img.src = url;
        // 2s safety timeout to skip stuck images
        setTimeout(() => settle(false), 2000);
      });
      if (ok) return url;
    } catch {}
  }
  return '/img/player-skin.png';
}

// Try backend first to retrieve player profile + stats
async function getPlayerFromBackend(query) {
  if (!API_BASE) return null;
  const q = encodeURIComponent(query);
  // Accept username or uuid; backend should resolve accordingly
  const url = `${API_BASE}/api/players/${q}`;
  try {
    const resp = await fetch(url, { headers: { Accept: 'application/json' } });
    if (!resp.ok) return null;
    const data = await resp.json();
    if (!data) return null;
    // Normalize fields from backend to our shape
    const uuidRaw = (data.uuid || data.id || '').toString();
    const uuid = uuidRaw.replace(/-/g, '');
    const playTimeMs =
      typeof data.playTimeMs === 'number'
        ? data.playTimeMs
        : typeof data.playTime === 'number'
          ? data.playTime
          : typeof data.playTimeHours === 'number'
            ? Math.round(data.playTimeHours * 3600 * 1000)
            : null;
    return {
      uuid: uuid && /^[0-9a-fA-F]{32}$/.test(uuid) ? uuid : null,
      displayName: data.username || data.name || query,
      role: data.role || 'player',
      isOnline: !!data.isOnline,
      firstSeen: data.firstSeen || null,
      lastSeen: data.lastSeen || null,
      playTime: playTimeMs,
      ggCoins: typeof data.ggCoins === 'number' ? data.ggCoins : undefined,
      wasBanned:
        data.was_banned === true || (typeof data.banned === 'number' && data.banned > 0) || false,
      bannedAt: typeof data.banned === 'number' ? data.banned : undefined,
      // Stats (optional, depending on backend availability)
      playedGamesByType: data.playedGamesByType || undefined,
      timeByGamePct: data.timeByGamePct || undefined,
      winRateByGame: data.winRateByGame || undefined,
    };
  } catch (_) {
    return null;
  }
}

// Public API fallback (provided URL)
const PUBLIC_API_BASE =
  (typeof import.meta !== 'undefined' && import.meta.env && import.meta.env.VITE_PUBLIC_API_BASE) ||
  '/api-public';
const PUBLIC_API_TIME_UNIT = (
  (typeof import.meta !== 'undefined' &&
    import.meta.env &&
    import.meta.env.VITE_PUBLIC_API_TIME_UNIT) ||
  'seconds'
).toLowerCase();

function toMsFromUnit(value) {
  if (typeof value !== 'number') return null;
  switch (PUBLIC_API_TIME_UNIT) {
    case 'ms':
    case 'millisecond':
    case 'milliseconds':
      return value;
    case 's':
    case 'sec':
    case 'second':
    case 'seconds':
      return Math.round(value * 1000);
    case 'm':
    case 'min':
    case 'minute':
    case 'minutes':
      return Math.round(value * 60 * 1000);
    case 'h':
    case 'hr':
    case 'hour':
    case 'hours':
      return Math.round(value * 3600 * 1000);
    default:
      return Math.round(value * 1000);
  }
}
async function getPlayerFromPublicApi(query) {
  const q = encodeURIComponent(query);
  const url = `${PUBLIC_API_BASE}/profile/${q}`;
  try {
    let resp = await fetch(url, { headers: { Accept: 'application/json' } });
    if (resp.status === 429) {
      // simple backoff and one retry
      await new Promise((r) => setTimeout(r, 800));
      resp = await fetch(url, { headers: { Accept: 'application/json' } });
    }
    if (resp.status === 429) {
      // signal rate limit to caller
      return { rateLimited: true };
    }
    if (!resp.ok) return null;
    const data = await resp.json();
    if (!data) return null;
    const uuidRaw = (data.uuid || '').toString();
    const uuid = uuidRaw.replace(/-/g, '');
    const onlineTime = data.online_time; // unit configurable by PUBLIC_API_TIME_UNIT
    const playTimeMs = typeof onlineTime === 'number' ? toMsFromUnit(onlineTime) : null;
    return {
      uuid: uuid && /^[0-9a-fA-F]{32}$/.test(uuid) ? uuid : null,
      displayName: data.username || query,
      role: (data.rank || 'player').toLowerCase(),
      isOnline: false,
      firstSeen: data.first_login || null,
      lastSeen: data.last_seen || null,
      playTime: playTimeMs,
      wasBanned:
        data.was_banned === true || (typeof data.banned === 'number' && data.banned > 0) || false,
      bannedAt: typeof data.banned === 'number' ? data.banned : undefined,
    };
  } catch (_) {
    return null;
  }
}

export function usePlayerStats(initialQuery) {
  const [query, setQuery] = useState(initialQuery || '');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [player, setPlayer] = useState(null);
  const [skinReady, setSkinReady] = useState(false);
  const skinViewerRef = useRef(null);
  const containerRef = useRef(null);
  const lastSearchIdRef = useRef(0);

  const search = useCallback(async (inputQuery) => {
    const mySearchId = ++lastSearchIdRef.current;
    const q = (inputQuery ?? '').trim();
    if (!q) {
      setError(ERROR_CODES.EMPTY);
      setPlayer(null);
      return;
    }
    setLoading(true);
    setError(null);
    // Force le reset du fade-in même si le skin ne change pas
    setSkinReady(false);
    try {
      // 1) Prefer public API
      const publicApi = await getPlayerFromPublicApi(q);
      if (mySearchId !== lastSearchIdRef.current) return; // stale
      if (publicApi && publicApi.rateLimited) {
        setError(ERROR_CODES.RATE_LIMIT);
        return;
      }
      if (publicApi && publicApi.uuid) {
        setPlayer({ ...publicApi, skinUrl: getSkinUrl(publicApi.uuid) });
        return;
      }

      // 1b) Try backend
      let backend = await getPlayerFromBackend(q);
      if (mySearchId !== lastSearchIdRef.current) return; // stale
      if (backend && backend.uuid) {
        setPlayer({ ...backend, skinUrl: getSkinUrl(backend.uuid) });
        return;
      }

      // 2) Fallback public APIs
      let uuid = null;
      let displayName = q;
      const cleaned = q.replace(/-/g, '');
      if (/^[0-9a-fA-F]{32}$/.test(cleaned)) {
        uuid = cleaned;
        const prof = await getProfileFromUUID(uuid);
        if (prof?.name) displayName = prof.name;
      } else {
        const prof = await getProfileFromUsername(q);
        if (prof) {
          uuid = prof.uuid;
          displayName = prof.name;
        }
      }
      if (mySearchId !== lastSearchIdRef.current) return; // stale
      if (!uuid || !/^[0-9a-fA-F]{32}$/.test(uuid)) {
        // Fallback Steve, mais toujours afficher le pseudo saisi
        setError(ERROR_CODES.NOT_FOUND);
        setPlayer({
          uuid: null,
          displayName: q, // toujours le texte de la recherche
          role: 'player',
          isOnline: false,
          firstSeen: null,
          lastSeen: null,
          playTime: null,
          skinUrl: getSkinUrl(null),
        });
        return;
      }
      setPlayer({
        uuid,
        displayName: displayName || q, // prefer resolved name when available
        role: 'player',
        isOnline: false,
        firstSeen: null,
        lastSeen: null,
        playTime: null,
        skinUrl: getSkinUrl(uuid),
      });
    } catch (e) {
      setError(ERROR_CODES.ERROR);
    } finally {
      if (mySearchId === lastSearchIdRef.current) setLoading(false);
    }
  }, []);

  useEffect(() => {
    if (initialQuery) {
      search(initialQuery);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [initialQuery]);

  // Skin viewer dynamic import
  useEffect(() => {
    let disposed = false;
    let rafId = null;
    let angle = 0;
    const rotationEnabledRef = { current: true };
    let ro = null;
    async function loadSkin() {
      if (!player || !containerRef.current) return;
      try {
        setSkinReady(false);
        // Resolve a working skin URL across multiple providers to avoid 500s
        const candidates = getCandidateSkinUrls(player.uuid);
        const skinUrlFinal = await resolveFirstWorkingImage(candidates);
        const { SkinViewer, IdleAnimation } = await import('skinview3d');
        if (disposed) return;
        containerRef.current.innerHTML = '';
        const width = containerRef.current.offsetWidth || 200;
        const height = containerRef.current.offsetHeight || 300;
        skinViewerRef.current = new SkinViewer({
          width,
          height,
          skin: skinUrlFinal,
          zoom: 0.0,
        });
        containerRef.current.appendChild(skinViewerRef.current.canvas);
        skinViewerRef.current.camera.position.set(0, 1.5, 42);
        if (typeof skinViewerRef.current.camera.lookAt === 'function') {
          // Some builds accept numbers; otherwise, this is a no-op if not supported
          try {
            skinViewerRef.current.camera.lookAt(0, 0.9, 0);
          } catch {}
        }
        const idle = new IdleAnimation();
        if (skinViewerRef.current.animations?.add) skinViewerRef.current.animations.add(idle);
        idle.speed = 0.6;
        if (idle.play) idle.play();

        // Slow continuous rotation around Y axis
        const animate = () => {
          if (disposed || !skinViewerRef.current) return;
          if (rotationEnabledRef.current) {
            angle += 0.003; // adjust speed here
          }
          if (skinViewerRef.current.playerObject) {
            skinViewerRef.current.playerObject.rotation.y = angle;
          }
          // render a frame if available
          if (typeof skinViewerRef.current.render === 'function') {
            skinViewerRef.current.render();
          }
          rafId = requestAnimationFrame(animate);
        };
        // Mark as ready juste après l'instanciation du viewer (image déjà préchargée)
        setSkinReady(true);
        rafId = requestAnimationFrame(animate);

        // Interactions: stop rotation on click, block zoom
        const canvas = skinViewerRef.current.canvas;
        if (canvas) {
          const onDown = () => {
            rotationEnabledRef.current = false;
          };
          const onUp = () => {
            rotationEnabledRef.current = true;
          };
          const onLeave = () => {
            rotationEnabledRef.current = true;
          };
          const onWheel = (e) => {
            e.preventDefault();
          };
          canvas.addEventListener('mousedown', onDown, { passive: true });
          canvas.addEventListener('mouseup', onUp, { passive: true });
          canvas.addEventListener('mouseleave', onLeave, { passive: true });
          canvas.addEventListener('touchstart', onDown, { passive: true });
          canvas.addEventListener('touchend', onUp, { passive: true });
          canvas.addEventListener('touchcancel', onLeave, { passive: true });
          canvas.addEventListener('wheel', onWheel, { passive: false });
          // Keep inside box on resize
          if (typeof ResizeObserver !== 'undefined') {
            ro = new ResizeObserver(() => {
              if (!skinViewerRef.current) return;
              const w = containerRef.current.offsetWidth || 200;
              const h = containerRef.current.offsetHeight || 300;
              try {
                if (typeof skinViewerRef.current.setSize === 'function') {
                  skinViewerRef.current.setSize(w, h);
                } else if (typeof skinViewerRef.current.resize === 'function') {
                  skinViewerRef.current.resize(w, h);
                } else {
                  // fallback: recreate
                  // trigger re-render by setting skin again
                  skinViewerRef.current.width = w;
                  skinViewerRef.current.height = h;
                }
              } catch {}
            });
            ro.observe(containerRef.current);
          }
          // Cleanup listeners on dispose
          const cleanup = () => {
            try {
              canvas.removeEventListener('mousedown', onDown);
            } catch {}
            try {
              canvas.removeEventListener('mouseup', onUp);
            } catch {}
            try {
              canvas.removeEventListener('mouseleave', onLeave);
            } catch {}
            try {
              canvas.removeEventListener('touchstart', onDown);
            } catch {}
            try {
              canvas.removeEventListener('touchend', onUp);
            } catch {}
            try {
              canvas.removeEventListener('touchcancel', onLeave);
            } catch {}
            try {
              canvas.removeEventListener('wheel', onWheel);
            } catch {}
          };
          // Attach cleanup to ref for use on unmount
          skinViewerRef.current._cleanupListeners = cleanup;
        }
      } catch (e) {
        // Même en cas d'échec, ne pas garder la zone cachée
        setSkinReady(true);
        /* ignore */
      }
    }
    loadSkin();
    return () => {
      disposed = true;
      if (rafId) cancelAnimationFrame(rafId);
      if (skinViewerRef.current?.dispose) skinViewerRef.current.dispose();
      if (skinViewerRef.current?._cleanupListeners) {
        try {
          skinViewerRef.current._cleanupListeners();
        } catch {}
      }
      if (ro) {
        try {
          ro.disconnect();
        } catch {}
      }
      setSkinReady(false);
    };
  }, [player]);

  return {
    state: { query, loading, error, player, skinReady },
    actions: { setQuery, search },
    utils: { formatDate, formatPlayTime, formatUUID },
    skinContainerRef: containerRef,
  };
}
