import React, { useEffect, useRef } from 'react';
import { useT } from '../i18n/LanguageProvider.jsx';
import { useReveal } from '../hooks/useReveal.js';

export default function Hero() {
  const t = useT();
  const wrapperRef = useRef(null);
  useEffect(() => {
    const container = wrapperRef.current;
    if (!container) return;
    const texts = Array.from(container.querySelectorAll('.title-text'));
    let idx = 0;
    texts.forEach((t) => t.classList.remove('active', 'slide-up'));
    if (texts[0]) texts[0].classList.add('active');
    const interval = setInterval(() => {
      if (document.hidden || texts.length === 0) return;
      texts[idx]?.classList.remove('active');
      idx = (idx + 1) % texts.length;
      const el = texts[idx];
      if (el) {
        el.classList.add('slide-up');
        void el.offsetWidth; // force reflow
        el.classList.add('active');
        setTimeout(() => el.classList.remove('slide-up'), 500);
      }
    }, 2500);
    return () => clearInterval(interval);
  }, []);

  useReveal();
  return (
    <section id="hero" className="hero section-auto-fade">
      <div className="container hero-flex">
        <div className="hero-left" style={{ width: '100%' }}>
          <span className="hero-brand">GANGUI</span>
          <h1 className="hero-title" tabIndex="-1">
            <span className="title-container" ref={wrapperRef}>
              <span className="title-wrapper">
                <span className="title-text" data-lang="hero-title-main">
                  {t('hero-title-main')}
                </span>
                <span className="title-text" data-lang="hero-title-unique">
                  {t('hero-title-unique')}
                </span>
                <span className="title-text" data-lang="hero-title-community">
                  {t('hero-title-community')}
                </span>
                <span className="title-text" data-lang="hero-title-adventure">
                  {t('hero-title-adventure')}
                </span>
              </span>
            </span>
          </h1>
          <p className="hero-desc" data-lang="hero-desc">
            {t('hero-desc')}
          </p>
          <div className="hero-buttons hero-buttons-inline">
            <a href="#join" className="btn btn-primary hover-scale-sm">
              <i className="fas fa-gamepad" />
              <span data-lang="hero-play">{t('hero-play')}</span>
            </a>
            <a
              href="https://discord.gg/7Kj8vsGdSr"
              className="btn btn-discord hover-scale-sm"
              target="_blank"
              rel="noreferrer"
            >
              <i className="fab fa-discord" />
              <span data-lang="hero-discord">{t('hero-discord')}</span>
            </a>
            <a href="#features" className="btn btn-secondary btn-outline hover-scale-sm">
              <i className="fas fa-bolt" />
              <span data-lang="hero-features">{t('hero-features')}</span>
            </a>
            <a
              href="https://github.com/ganguimc/"
              className="btn btn-dark btn-outline btn-border-light hover-scale-sm"
              target="_blank"
              rel="noreferrer"
            >
              <i className="fab fa-github" />
              <span>GitHub</span>
            </a>
          </div>
        </div>
      </div>
      <div className="hero-bg" />
    </section>
  );
}
