import React, { useEffect, useState, useRef } from 'react';
import { useLanguage, useT } from '../i18n/LanguageProvider.jsx';
import { Sun, Moon } from 'lucide-react';
import { availableLanguages, languageNames } from '../i18n/config.js';

function useThemeToggle() {
  const [theme, setTheme] = useState(() => {
    if (typeof window === 'undefined') return 'dark';
    return localStorage.getItem('theme') || 'dark';
  });
  useEffect(() => {
    const html = document.documentElement;
    html.classList.remove('dark-theme', 'light-theme');
    html.classList.add(theme + '-theme');
    localStorage.setItem('theme', theme);
  }, [theme]);
  const toggle = () => setTheme((t) => (t === 'dark' ? 'light' : 'dark'));
  return { theme, toggle };
}

export default React.memo(function Header() {
  const { lang, setLang } = useLanguage();
  const t = useT();
  const { theme, toggle } = useThemeToggle();
  const [langMenuOpen, setLangMenuOpen] = useState(false);
  const langBtnRef = useRef(null);
  const base = import.meta.env.BASE_URL || '/';

  useEffect(() => {
    function handleOutside(e) {
      if (!langBtnRef.current) return;
      if (!langBtnRef.current.closest('.lang-dropdown').contains(e.target)) {
        setLangMenuOpen(false);
      }
    }
    document.addEventListener('click', handleOutside);
    return () => document.removeEventListener('click', handleOutside);
  }, []);

  // Scroll header style
  const [scrolled, setScrolled] = useState(false);
  useEffect(() => {
    function onScroll() {
      setScrolled(window.scrollY > 50);
    }
    onScroll();
    window.addEventListener('scroll', onScroll);
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  const navItems = [
    { href: '#hero', key: 'nav-home' },
    { href: '#features', key: 'nav-features' },
    { href: '#faq', key: 'nav-faq' },
    { href: '#join', key: 'nav-join' },
    { href: '#stats', key: 'nav-stats' },
  ];

  return (
    <header className={'header' + (scrolled ? ' scrolled' : '')}>
      <div className="container">
        <div className="navbar">
          <div className="logo">
            <a href="#hero">
              <img className="logo-dark" src={base + 'img/logo_dark.svg'} alt="Logo dark" />
              <img className="logo-light" src={base + 'img/logo_light.svg'} alt="Logo light" />
              <span className="logo-text" data-lang="logo-brand">
                GANGUI
              </span>
            </a>
          </div>
          <nav className="nav-menu nav-menu-right">
            <ul>
              {navItems.map((item) => {
                const currentHash =
                  typeof window !== 'undefined' ? window.location.hash || '#hero' : '#hero';
                const active = currentHash === item.href;
                return (
                  <li key={item.key}>
                    <a href={item.href} data-lang={item.key} className={active ? 'active' : ''}>
                      {t(item.key)}
                    </a>
                  </li>
                );
              })}
            </ul>
          </nav>
          <div className="header-actions">
            <span className="nav-separator" />
            <div className={'lang-dropdown' + (langMenuOpen ? ' open' : '')}>
              <button
                ref={langBtnRef}
                className="lang-btn globe"
                aria-label={t('language-selector', 'Select language')}
                aria-expanded={langMenuOpen}
                aria-haspopup="menu"
                id="langDropdownBtn"
                onClick={() => setLangMenuOpen((o) => !o)}
              >
                <svg width="22" height="22" viewBox="0 0 24 24" fill="none" aria-hidden="true">
                  <circle cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="2" />
                  <path
                    d="M2 12h20M12 2a15 15 0 010 20M12 2a15 15 0 000 20"
                    stroke="currentColor"
                    strokeWidth="2"
                  />
                </svg>
                <span className="lang-current" data-lang="current-lang">
                  {t('current-lang')}
                </span>{' '}
                <i className="fas fa-chevron-down" aria-hidden="true" />
              </button>
              <div className="lang-menu" role="menu" aria-labelledby="langDropdownBtn" id="langDropdownMenu">
                {availableLanguages.map((l) => {
                  const label = languageNames[l] || l;
                  return (
                    <button
                      key={l}
                      className={'lang-option' + (l === lang ? ' active' : '')}
                      role="menuitem"
                      aria-current={l === lang ? 'true' : undefined}
                      onClick={() => {
                        setLang(l);
                        setLangMenuOpen(false);
                      }}
                    >
                      {label}
                    </button>
                  );
                })}
              </div>
            </div>
            <button className="nav-icon theme-toggle" aria-label={theme === 'dark' ? t('switch-to-light-theme', 'Switch to light theme') : t('switch-to-dark-theme', 'Switch to dark theme')} onClick={toggle}>
              {theme === 'dark' ? <Sun size={20} aria-hidden="true" /> : <Moon size={20} aria-hidden="true" />}
            </button>
          </div>
        </div>
      </div>
    </header>
  );
});
