import React, { useEffect, useMemo, useState } from 'react';
import { Search as SearchIcon } from 'lucide-react';
import { useT } from '../i18n/LanguageProvider.jsx';
import { useReveal } from '../hooks/useReveal.js';
import { usePlayerStats } from '../hooks/usePlayerStats.js';

// formatters now defined inside component to leverage i18n

// --- Consistent chart system (shared theme, legend, and three chart types) ---
const chartTheme = {
  palette: ['#5865F2', '#fc8b4f', '#85a2d5', '#ffd166', '#3ba779', '#e86a92'],
  bg: 'var(--bg-dark)',
  grid: 'var(--stroke-base)',
  axis: 'var(--stroke-base)',
  text: 'var(--text-secondary)',
  title: 'var(--text-primary)',
  value: 'var(--text-primary)',
};

const defaultCategoryOrder = [
  'SkyWars',
  'Rush',
  'HikaBrain',
  'BedWars',
  'UHC',
  'BuildBattle',
  'Duels',
  'Other',
];

function getCategoryOrder(groups) {
  // Build a stable order: use default if present, otherwise union of keys in order of appearance
  const present = new Set();
  const ordered = [];
  const add = (k) => {
    if (!present.has(k)) {
      present.add(k);
      ordered.push(k);
    }
  };
  defaultCategoryOrder.forEach((k) => {
    for (const g of groups) {
      if (g && Object.prototype.hasOwnProperty.call(g, k)) {
        add(k);
        break;
      }
    }
  });
  for (const g of groups) {
    Object.keys(g || {}).forEach(add);
  }
  return ordered;
}

function colorFor(cat, categories) {
  const idx = Math.max(0, categories.indexOf(cat));
  return chartTheme.palette[idx % chartTheme.palette.length];
}

function DonutChart({ data = {}, size = 660, categories, center }) {
  const total = Math.max(
    1,
    Object.values(data).reduce((s, v) => s + (v || 0), 0)
  );
  let acc = 0;
  const entries = Object.entries(data);
  const [hover, setHover] = React.useState(null);
  const r = size / 2;
  const hole = r * 0.58;
  const cx = r,
    cy = r;
  const ptc = (a) => {
    const rad = ((a - 90) * Math.PI) / 180;
    return { x: cx + r * Math.cos(rad), y: cy + r * Math.sin(rad) };
  };
  return (
    <svg
      style={{ width: '100%', height: 'auto' }}
      viewBox={`0 0 ${size} ${size}`}
      role="img"
      aria-label="Donut chart"
      preserveAspectRatio="xMidYMid meet"
    >
      {entries.map(([k, v]) => {
        const val = v || 0;
        const ang = (val / total) * 360;
        const start = acc;
        const end = acc + ang;
        acc = end;
        const s = ptc(start),
          e = ptc(end),
          la = end - start > 180 ? 1 : 0;
        const d = `M ${cx} ${cy} L ${s.x} ${s.y} A ${r} ${r} 0 ${la} 1 ${e.x} ${e.y} Z`;
        const isHover = hover?.k === k;
        return (
          <path
            key={k}
            d={d}
            fill={colorFor(k, categories)}
            stroke={chartTheme.bg}
            strokeWidth={isHover ? 0 : 0}
            opacity={hover ? (isHover ? 1 : 0.6) : 1}
            onMouseEnter={() => setHover({ k, v: val })}
            onMouseLeave={() => setHover(null)}
          />
        );
      })}
      <circle cx={cx} cy={cy} r={hole} fill={chartTheme.bg} />
      {hover || center ? (
        <>
          <text
            x={cx}
            y={cy - 2}
            textAnchor="middle"
            fontSize={Math.max(12, size * 0.11)}
            fontWeight="800"
            fill={chartTheme.title}
          >
            {hover ? `${hover.v}%` : center.value}
          </text>
          <text
            x={cx}
            y={cy + 14}
            textAnchor="middle"
            fontSize={Math.max(9, size * 0.07)}
            fill={chartTheme.text}
          >
            {hover ? hover.k : center.label || ''}
          </text>
        </>
      ) : null}
    </svg>
  );
}

function HBarChart({ data = {}, barHeight = 12, categories }) {
  const entries = Object.entries(data);
  const max = Math.max(1, ...entries.map(([, v]) => v || 0));
  return (
    <div style={{ display: 'flex', flexDirection: 'column', gap: '0.75rem' }}>
      {entries.map(([k, v]) => {
        const pct = (v / max) * 100;
        return (
          <div
            key={k}
            style={{
              display: 'grid',
              gridTemplateColumns: '120px 1fr 52px',
              gap: '0.75rem',
              alignItems: 'center',
            }}
          >
            <div style={{ color: chartTheme.text, fontSize: '0.95rem' }}>{k}</div>
            <div
              style={{
                height: barHeight,
                background: 'var(--bg-lighter)',
                borderRadius: 999,
                overflow: 'hidden',
                position: 'relative',
                border: '1px solid var(--bg-light)',
              }}
            >
              <div
                style={{
                  width: `${pct}%`,
                  height: barHeight,
                  background: colorFor(k, categories),
                  borderRadius: 999,
                }}
              />
            </div>
            <div style={{ textAlign: 'right', fontWeight: 700, color: chartTheme.value }}>{v}</div>
          </div>
        );
      })}
    </div>
  );
}

function VBarChart({
  data = {},
  height = 260,
  barWidth = 22,
  gap = 26,
  categories,
  yTicks = [0, 25, 50, 75, 100],
  yLabel,
  valueSuffix = '',
  showValues = true,
}) {
  const entries = Object.entries(data);
  const left = 40;
  const top = 10;
  const bottom = 46;
  const right = 10;
  const plotH = height - top - bottom;
  // Ensure minimum width for chart, so bars never get squished
  const minWidth = 420;
  const width = Math.max(
    minWidth,
    left + right + entries.length * barWidth + (entries.length - 1) * gap
  );
  const xPos = (i) => left + i * (barWidth + gap);
  const yPos = (v) => top + (1 - Math.max(0, Math.min(100, v)) / 100) * plotH;
  return (
    <svg
      width={width}
      height={height}
      style={{ minWidth: minWidth, maxWidth: '100%' }}
      viewBox={`0 0 ${width} ${height}`}
      role="img"
      aria-label="Barres verticales"
    >
      {/* baseline axis */}
      <line
        x1={left}
        x2={left}
        y1={top}
        y2={top + plotH}
        stroke={chartTheme.grid}
        strokeWidth="1"
        opacity="0.6"
      />
      {/* gridlines */}
      {yTicks.map((g) => {
        const y = yPos(g);
        return (
          <g key={g}>
            <line
              x1={left}
              x2={width - right}
              y1={y}
              y2={y}
              stroke={chartTheme.grid}
              strokeWidth="1"
              opacity="0.6"
            />
            <text x={left - 6} y={y + 4} textAnchor="end" fontSize="10" fill={chartTheme.text}>
              {g}
            </text>
          </g>
        );
      })}
      {/* y-axis label */}
      {yLabel ? (
        <text
          x={12}
          y={top + plotH / 2}
          transform={`rotate(-90 12 ${top + plotH / 2})`}
          fontSize="11"
          fill={chartTheme.text}
        >
          {yLabel}
        </text>
      ) : null}
      {/* bars */}
      {entries.map(([k, v], i) => {
        const x = xPos(i);
        const y = yPos(v);
        const h = top + plotH - y;
        const label = `${Math.round(v)}${valueSuffix}`;
        const chipW = Math.max(28, label.length * 7 + 8);
        return (
          <g key={k}>
            <rect x={x} y={y} width={barWidth} height={h} fill={colorFor(k, categories)} rx={4} />
            {showValues ? (
              <>
                {/* value chip */}
                <rect
                  x={x + barWidth / 2 - chipW / 2}
                  y={y - 18}
                  width={chipW}
                  height={14}
                  rx={7}
                  fill="var(--bg-dark)"
                  stroke="var(--bg-light)"
                  strokeWidth="1"
                />
                <text
                  x={x + barWidth / 2}
                  y={y - 7}
                  textAnchor="middle"
                  fontSize="10"
                  fill={chartTheme.value}
                >
                  {label}
                </text>
              </>
            ) : null}
            <text
              x={x + barWidth / 2}
              y={height - 10}
              textAnchor="middle"
              fontSize="11"
              fill={chartTheme.text}
            >
              {k}
            </text>
          </g>
        );
      })}
    </svg>
  );
}

// Small KPI card
function KPI({ icon, label, value, hint }) {
  return (
    <div
      style={{
        display: 'flex',
        alignItems: 'center',
        gap: '0.9rem',
        background: 'var(--bg-lighter)',
        border: '1px solid var(--bg-light)',
        borderRadius: 14,
        padding: '0.85rem 1rem',
      }}
    >
      <div
        style={{
          width: 34,
          height: 34,
          borderRadius: 8,
          background: 'var(--bg-dark)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          border: '1px solid var(--bg-light)',
        }}
      >
        <i className={icon} aria-hidden style={{ color: 'var(--primary)' }} />
      </div>
      <div style={{ display: 'flex', flexDirection: 'column', lineHeight: 1.1 }}>
        <div style={{ fontSize: 12, color: 'var(--text-secondary)' }}>{label}</div>
        <div style={{ fontWeight: 800, fontSize: 18, color: 'var(--text-primary)' }}>{value}</div>
        {hint ? <div style={{ fontSize: 11, color: 'var(--text-muted)' }}>{hint}</div> : null}
      </div>
    </div>
  );
}

export default function Stats() {
  const t = useT();
  const formatDate = (ts) => {
    if (!ts) return t('stats-na', 'N/A');
    return new Date(ts).toLocaleDateString();
  };
  const formatHours = (h) => {
    if (h == null) return t('stats-na', 'N/A');
    const hrs = Math.floor(h);
    const mins = Math.round((h - hrs) * 60);
    return `${hrs}h ${mins}m`;
  };
  // Hook for backend search + skin render

  const { state, actions, skinContainerRef } = usePlayerStats();
  const { player: hookPlayer, loading, error, query, skinReady } = state;
  const { setQuery, search } = actions;

  // Utilise directement hookPlayer, plus de state local
  const player = hookPlayer
    ? {
        username: hookPlayer.displayName || hookPlayer.username,
        role: hookPlayer.role || 'player',
        isOnline: !!hookPlayer.isOnline,
        uuid: hookPlayer.uuid,
        firstSeen: hookPlayer.firstSeen || null,
        lastSeen: hookPlayer.lastSeen || null,
        playTimeHours:
          typeof hookPlayer.playTime === 'number' ? hookPlayer.playTime / 3600000 : null,
        ggCoins: typeof hookPlayer.ggCoins === 'number' ? hookPlayer.ggCoins : 0,
        wasBanned: hookPlayer.wasBanned === true,
        bannedAt: typeof hookPlayer.bannedAt === 'number' ? hookPlayer.bannedAt : null,
        playedGamesByType: hookPlayer.playedGamesByType || {},
        timeByGamePct: hookPlayer.timeByGamePct || {},
        winRateByGame: hookPlayer.winRateByGame || {},
      }
    : null;

  useReveal();

  // Debounced live search on input
  useEffect(() => {
    // don't trigger on initial empty state
    const q = (query || '').trim();
    if (!q) {
      // Pas de recherche, le hook retournera player === null
      return;
    }
    const handle = setTimeout(() => {
      search(q);
    }, 450);
    return () => clearTimeout(handle);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [query]);

  const totals = useMemo(() => {
    if (!player) return { games: 0 };
    return {
      games: Object.values(player.playedGamesByType || {}).reduce((a, b) => a + (b || 0), 0),
    };
  }, [player]);

  const categories = useMemo(
    () =>
      getCategoryOrder([player?.timeByGamePct, player?.playedGamesByType, player?.winRateByGame]),
    [player]
  );

  const metrics = useMemo(() => {
    if (!player) return null;
    const total =
      Object.values(player.playedGamesByType || {}).reduce((a, b) => a + (b || 0), 0) || 1;
    const entries = Object.entries(player.playedGamesByType || {});
    const top = entries.reduce((m, [k, v]) => (v > (m?.v || -1) ? { k, v } : m), null) || {
      k: '-',
      v: 0,
    };
    const wr =
      Object.entries(player.winRateByGame || {}).reduce(
        (s, [k, p]) => s + (p || 0) * (player.playedGamesByType?.[k] || 0),
        0
      ) / total;
    const topShare = Math.round((top.v / total) * 100);
    return {
      totalGames: total,
      topGame: top.k,
      topShare,
      weightedWR: Math.round(wr || 0),
    };
  }, [player]);

  return (
    <section id="stats" className="player-stats section-auto-fade">
      <div className="container stats-container">
        {/* Search bar */}
        <div
          className="stats-search"
          style={!player ? { display: 'flex', justifyContent: 'center' } : undefined}
        >
          <form
            className="stats-search-form"
            onSubmit={(e) => {
              e.preventDefault();
              e.stopPropagation();
              // On relance la recherche uniquement si la query n'est pas vide
              if ((query || '').trim()) {
                search(query);
              }
              // Sinon, ne rien faire (pas de reload, pas de scroll)
              return false;
            }}
          >
            <div className="search-field search-field-right">
              <input
                id="player-search-input"
                className="search-input"
                type="text"
                placeholder={t('stats-search-input')}
                aria-label={t('stats-search-input')}
                value={query}
                onChange={(e) => setQuery(e.target.value)}
              />
              <SearchIcon className="search-icon" aria-hidden />
            </div>
          </form>
        </div>
        {/* Top row + analytics only if a player is selected */}
        {player ? (
          <>
            <div className="stats-grid">
              {/* LEFT: 3D skin render */}
              <div className="skin-card">
                <div
                  id="player-skin-viewer-container"
                  ref={skinContainerRef}
                  key={player?.uuid || 'fallback'}
                  className={`skin-fade-viewer${skinReady ? ' ready' : ''}`}
                  style={{ width: '100%', height: '100%' }}
                  role="img"
                  aria-label={t('player-skin-viewer', '3D view of player skin')}
                />
              </div>

              {/* MIDDLE: Title + GGCoins + two rows of stats */}
              <div className="stats-col">
                <div style={{ display: 'flex', alignItems: 'center', gap: '0.75rem' }}>
                  <span
                    className={`player-role ${player.role === 'player' ? 'member' : player.role} role-badge`}
                    data-lang={`role-${player.role === 'player' ? 'member' : player.role}`}
                  >
                    {t(
                      `role-${player.role === 'player' ? 'member' : player.role}`,
                      (player.role === 'player' ? 'MEMBER' : player.role).toUpperCase()
                    )}
                  </span>
                  <h1 className="player-username" tabIndex="-1">{player.username}</h1>
                </div>
                <div
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: '0.65rem',
                    flexWrap: 'wrap',
                  }}
                >
                  <div className="coins">
                    <i className="fas fa-coins" aria-hidden />
                    <span className="coins-value">{(player.ggCoins || 0).toLocaleString()}</span>
                  </div>
                  {Number.isFinite(player.bannedAt) && player.bannedAt > 0 ? (
                    <div className="tag tag-danger" title={t('stats-banned', 'Banned')}>
                      <i className="fas fa-ban" aria-hidden />
                      <span className="tag-value">{t('stats-banned', 'BANNED')}</span>
                    </div>
                  ) : player.wasBanned ? (
                    <div className="tag tag-danger" title={t('stats-was-banned', 'Was banned')}>
                      <i className="fas fa-exclamation-triangle" aria-hidden />
                      <span className="tag-value">{t('stats-was-banned', 'WAS BANNED')}</span>
                    </div>
                  ) : null}
                </div>
                <div className="kpi-grid">
                  <div className="kpi-card">
                    <div className="kpi-icon">
                      <i className="fas fa-calendar-plus" aria-hidden />
                    </div>
                    <div className="kpi-meta">
                      <div className="kpi-label" data-lang="stats-first-seen">
                        {t('stats-first-seen')}
                      </div>
                      <div className="kpi-value">{formatDate(player.firstSeen)}</div>
                    </div>
                  </div>
                  <div className="kpi-card">
                    <div className="kpi-icon">
                      <i className="fas fa-calendar-check" aria-hidden />
                    </div>
                    <div className="kpi-meta">
                      <div className="kpi-label" data-lang="stats-last-seen">
                        {t('stats-last-seen')}
                      </div>
                      <div className="kpi-value">{formatDate(player.lastSeen)}</div>
                    </div>
                  </div>
                  <div className="kpi-card">
                    <div className="kpi-icon">
                      <i className="fas fa-hourglass-half" aria-hidden />
                    </div>
                    <div className="kpi-meta">
                      <div className="kpi-label" data-lang="stats-play-time">
                        {t('stats-play-time')}
                      </div>
                      <div className="kpi-value">{formatHours(player.playTimeHours)}</div>
                    </div>
                  </div>
                  <div className="kpi-card">
                    <div className="kpi-icon">
                      <i className="fas fa-gamepad" aria-hidden />
                    </div>
                    <div className="kpi-meta">
                      <div className="kpi-label" data-lang="stats-played-games">
                        {t('stats-played-games', 'Games played')}
                      </div>
                      <div className="kpi-value">{totals.games.toLocaleString()}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/* Analytics panel below profile */}
            <div className="analytics-panel">
              <div className="bento-grid">
                {/* Left: Donut chart */}
                <div className="bento-donut">
                  <div className="donut-wrap">
                    <DonutChart
                      data={player.timeByGamePct}
                      size={360}
                      categories={categories}
                      center={{
                        value:
                          Object.keys(player.timeByGamePct || {}).length > 0
                            ? `${Math.max(...Object.values(player.timeByGamePct || {}))}%`
                            : '—',
                        label:
                          Object.keys(player.playedGamesByType || {}).length > 0
                            ? (Object.entries(player.playedGamesByType || {}).sort(
                                (a, b) => b[1] - a[1]
                              )[0] || ['-'])[0]
                            : '',
                      }}
                    />
                  </div>
                </div>
                {/* Right: KPI grid and VBarChart below */}
                <div className="bento-right">
                  <div className="kpi-grid">
                    <div className="kpi-card">
                      <div className="kpi-icon">
                        <i className="fas fa-gamepad" aria-hidden />
                      </div>
                      <div className="kpi-meta">
                        <div className="kpi-label" data-lang="stats-played-games">
                          {t('stats-played-games', 'Games played')}
                        </div>
                        <div className="kpi-value">{metrics?.totalGames?.toLocaleString()}</div>
                      </div>
                    </div>
                    <div className="kpi-card">
                      <div className="kpi-icon">
                        <i className="fas fa-trophy" aria-hidden />
                      </div>
                      <div className="kpi-meta">
                        <div className="kpi-label" data-lang="stats-global-winrate">
                          {t('stats-global-winrate', 'Global win rate')}
                        </div>
                        <div className="kpi-value">{`${metrics?.weightedWR ?? 0}%`}</div>
                      </div>
                    </div>
                    <div className="kpi-card">
                      <div className="kpi-icon">
                        <i className="fas fa-chart-pie" aria-hidden />
                      </div>
                      <div className="kpi-meta">
                        <div className="kpi-label" data-lang="stats-most-played-mode">
                          {t('stats-most-played-mode', 'Most played mode')}
                        </div>
                        <div className="kpi-value">{metrics?.topGame || '-'}</div>
                        <div className="kpi-hint">{`${metrics?.topShare ?? 0}% ${t('stats-of-games', 'of games')}`}</div>
                      </div>
                    </div>
                    <div className="kpi-card">
                      <div className="kpi-icon">
                        <i className="fas fa-clock" aria-hidden />
                      </div>
                      <div className="kpi-meta">
                        <div className="kpi-label" data-lang="stats-play-time">
                          {t('stats-play-time')}
                        </div>
                        <div className="kpi-value">{formatHours(player.playTimeHours)}</div>
                      </div>
                    </div>
                  </div>
                  <div className="vbar-card">
                    <div className="vbar-card-title" data-lang="stats-winrate-by-game">
                      {t('stats-winrate-by-game', 'Winrate by game')}
                    </div>
                    <div className="chart-scroll-x">
                      <VBarChart
                        data={player.winRateByGame || {}}
                        categories={categories}
                        yLabel={null}
                        valueSuffix=""
                        showValues={false}
                        barWidth={32}
                        gap={28}
                        height={260}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </>
        ) : null}

        <div className="stats-note">
          {loading ? (
            <p>{t('stats-loading')}</p>
          ) : error === 'ratelimited' ? (
            <p>{t('stats-rate-limited', 'Too many requests. Please try again in a moment.')}</p>
          ) : error === 'notfound' ? (
            <p>{t('stats-player-not-found')}</p>
          ) : error === 'empty' ? (
            <p>{t('stats-empty-query')}</p>
          ) : error === 'error' ? (
            <p>{t('stats-search-error')}</p>
          ) : null}
        </div>
      </div>
    </section>
  );
}
