import React, { createContext, useContext, useEffect, useState, useCallback, useRef } from 'react';
import { availableLanguages, defaultLanguage } from './config.js';

// Very small YAML parser for flat key: value pairs (no nesting needed here)
function parseFlatYAML(src) {
  const out = {};
  if (!src) return out;
  const lines = src.split(/\r?\n/);
  for (const raw of lines) {
    const line = raw.trim();
    if (!line || line.startsWith('#')) continue;
    const idx = line.indexOf(':');
    if (idx === -1) continue;
    const key = line.slice(0, idx).trim();
    let val = line.slice(idx + 1).trim();
    if ((val.startsWith('"') && val.endsWith('"')) || (val.startsWith("'") && val.endsWith("'"))) {
      val = val.slice(1, -1);
    }
    out[key] = val;
  }
  return out;
}

async function loadLocale(lang) {
  try {
    const mod = await import(`./locales/${lang}.yml?raw`);
    const text = mod.default || mod;
    return parseFlatYAML(text);
  } catch (e) {
    console.warn('[i18n] Failed loading locale', lang, e);
    return {};
  }
}

// Cache to avoid re-loading
const cache = {};

// Synchronous fallback: eagerly read default language so no keys flash on first paint
let baseFallback = {};
try {
  // Eagerly import all locales as raw text, then pick the default one
  const rawLocales = import.meta.glob('./locales/*.yml', { as: 'raw', eager: true });
  const defPath = `./locales/${defaultLanguage}.yml`;
  const raw = rawLocales[defPath] || '';
  baseFallback = parseFlatYAML(raw);
} catch (e) {
  // If glob not supported in some context, ignore and keep empty (will async load)
  baseFallback = {};
}

const LanguageContext = createContext({
  lang: defaultLanguage,
  setLang: () => {},
  t: (key, opts) => key,
});

export function LanguageProvider({ children }) {
  const [lang, setLang] = useState(() => localStorage.getItem('lang') || defaultLanguage);
  // Initialize with base fallback for immediate translations
  const [dicts, setDicts] = useState({ base: baseFallback, active: baseFallback });
  const loadingRef = useRef(false);

  // Initial + active language load
  useEffect(() => {
    let cancelled = false;
    (async () => {
      // Ensure base (default) is cached; prefer eager fallback
      cache[defaultLanguage] = cache[defaultLanguage] || baseFallback || {};
      if (Object.keys(cache[defaultLanguage]).length === 0) {
        cache[defaultLanguage] = await loadLocale(defaultLanguage);
      }
      let active = cache[lang];
      if (!active) {
        loadingRef.current = true;
        active = await loadLocale(lang);
        cache[lang] = active;
        loadingRef.current = false;
      }
      if (!cancelled) {
        setDicts({
          base: cache[defaultLanguage] || {},
          active: active && Object.keys(active).length ? active : cache[defaultLanguage] || {},
        });
      }
    })();
    return () => {
      cancelled = true;
    };
  }, [lang]);

  useEffect(() => {
    localStorage.setItem('lang', lang);
  }, [lang]);

  const t = useCallback(
    (key, opts = {}) => {
      const { base, active } = dicts;
      let val = active[key] || base[key] || opts.defaultValue || key;
      if (opts.html) return val;
      return val;
    },
    [dicts]
  );

  return (
    <LanguageContext.Provider value={{ lang, setLang, t }}>{children}</LanguageContext.Provider>
  );
}

export function useLanguage() {
  return useContext(LanguageContext);
}
export function useT() {
  return useContext(LanguageContext).t;
}
